@import '../../scss/variabels';


.login-wrap {
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
}


.preloader {
	display: flex;
	justify-content: center;
	height: 100vh;
	align-items: center;
}

@-webkit-keyframes animate-svg-stroke-1 {
	0% {
		stroke-dashoffset: 1109.9151611328125px;
		stroke-dasharray: 1109.9151611328125px;
	}
	
	100% {
		stroke-dashoffset: 0;
		stroke-dasharray: 1109.9151611328125px;
	}
}

@keyframes animate-svg-stroke-1 {
	0% {
		stroke-dashoffset: 1109.9151611328125px;
		stroke-dasharray: 1109.9151611328125px;
	}
	
	100% {
		stroke-dashoffset: 0;
		stroke-dasharray: 1109.9151611328125px;
	}
}

@-webkit-keyframes animate-svg-fill-1 {
	0% {
		fill: transparent;
	}
	
	100% {
		fill: rgb(0, 0, 0);
	}
}

@keyframes animate-svg-fill-1 {
	0% {
		fill: transparent;
	}
	
	100% {
		fill: rgb(0, 0, 0);
	}
}

.svg-elem-1 {
	-webkit-animation: animate-svg-stroke-1 2s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s both,
	animate-svg-fill-1 0.7s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.8s both;
	animation: animate-svg-stroke-1 2s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s both,
	animate-svg-fill-1 0.7s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.8s both;
}

